h4{
    display:flex;
    align-items:center;
}

.header-info{
    display:flex;
    justify-content: center;
    align-items: center;
    margin-top:40px;
    margin-bottom:40px;
}

.description-input{
    width:40%;
}

.clause-tooltip{
    font-size:14px;
    width:auto;
    line-height:1.3;
    padding-top:0px;
    margin-bottom:0px;
}

.clause-holder{
    display:grid;
    grid-template-columns: 45% 55%;
    padding-right:7%;
    padding-left:7%;
    padding-top: 20px;
    padding-bottom: 20px;
}

.clause-left-side{
    grid-column-start: 1;
    grid-column-end: 2;
    text-align:left;
    padding-top:30px;
    padding-bottom:30px;
}

.clause-right-side{
    grid-column-start: 2;
    grid-column-end: 3;
    padding-top:30px;
    padding-bottom:30px;
}

.create-input{
    margin-left:5px;
    margin-right:5px;
    margin-bottom:10px;
}

.var-input{
    width:20%;
    margin-right:15px;
}

.var-desc-input{
    width:30%;
}

.title-input{
    margin-right:60px;
    width:15%;
}

.var-holder{
    margin-top:30px;
    margin-bottom:30px;
    margin-left: 5%;
}

.add-var{
    display:flex;
    justify-content: center;
    align-items: center;
}

.add-clause{
    display:flex;
    justify-content: center;
    align-items: center;
    margin-top:15px;
    margin-bottom:20px;
}

.exit-options{
    display:grid;
    grid-template-columns: 45% 55%;
    padding-left:5%;
    padding-top: 20px;
    padding-bottom: 20px;
}

.exit-buttons{
    grid-column-start: 1;
    grid-column-end: 2;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.exit-buttons button{
    margin-top:15px;
    min-width:8rem;
}

.add-button button{
    min-width:12rem;
}

.clause-border::after, .header-border::after {
    content: ""; 
    display: block; 
    margin: 0 auto; 
    width: 60%; 
    border-bottom: 3px solid black; 
    border-color:  #f2eaef;
    border-radius: 5px;
}